import React, { useState, useEffect, useRef } from "react";
import {
  Layout,
  Breadcrumb,
  Input,
  Button,
  Table,
  Tooltip,
  Pagination,
  Modal,
  Form,
  Cascader,
} from "antd";
import * as actions from "../../store/actions/actions";
import { EditOutlined, EnvironmentFilled } from "@ant-design/icons";
import { connect } from "react-redux";
import cityData from "./citydata";
import OrderStep from "../../components/Step/Step";
import { formatDate } from "../../utils/utils";
const { Content } = Layout;
const { Search } = Input;
function Order(props) {
  const colums = [
    {
      title: "#",
      dataIndex: "",
      render: (text, record, index) => {
        return (
          <>
            <span>{index + 1}</span>
          </>
        );
      },
    },
    {
      title: "",
      dataIndex: "order_number",
      width: 300,
    },
    {
      title: "",
      dataIndex: "order_price",
      width: 230,
    },
    {
      title: "",
      dataIndex: "pay_status",
      width: 230,
    },
    {
      title: "",
      dataIndex: "is_send",
      width: 240,
    },
    {
      title: "",
      dataIndex: "create_time",
      width: 300,
    },

    {
      title: "",
      dataIndex: "action",
      width: 250,
      render: (text, record) => (
        <div className="operation">
          <Tooltip title="">
            <Button
              icon={<EditOutlined />}
              size="mini"
              type="primary"
              onClick={() => setaddvisible(true)}
            ></Button>
          </Tooltip>
          <Tooltip title="">
            <Button
              className="success"
              icon={<EnvironmentFilled />}
              size="mini"
              onClick={() => showDelbox(record)}
            ></Button>
          </Tooltip>
        </div>
      ),
    },
  ];
  const goHome = () => {
    props.history.push("/welcome");
  };
  // 
  const [queryInfo, setqueryInfo] = useState({
    query: "",
    pagenum: 1,
    pagesize: 5,
  });
  const [pageNums] = useState([5, 10, 15]);
  // 
  const [form] = Form.useForm();
  // 
  const [addvisible, setaddvisible] = useState(false);

  // 
  const changeSearchValue = (e) => {
    setqueryInfo({
      ...queryInfo,
      query: e.target.value,
    });
  };
  // 
  const changePageNum = (page, pageSize) => {
    setqueryInfo({
      ...queryInfo,
      pagenum: page,
      pagesize: pageSize,
    });
  };
  // 
  const addAddressColsed = () => {
    setaddvisible(false);
    form.resetFields();
  };
  // modal/
  const [isModalVisible, setisModalVisible] = useState(false);
  // 
  useEffect(() => {
    props.getOrders(queryInfo);
  }, [queryInfo]);

  // 
  useEffect(() => {
    props.getLog();
  }, [isModalVisible]);
  // 
  const showDelbox = (info) => {
    controlVisible(true);
  };
  // 
  function controlVisible(flag) {
    setisModalVisible(flag);
  }
  return (
    <>
      <Layout className="site-layout">
        <Content style={{ margin: " 16px" }}>
          {/*  */}
          <Breadcrumb style={{ margin: "16px 0" }}>
            <Breadcrumb.Item onClick={goHome}></Breadcrumb.Item>
            <Breadcrumb.Item></Breadcrumb.Item>
            <Breadcrumb.Item></Breadcrumb.Item>
          </Breadcrumb>
          {/*  */}
          <div
            className="site-layout-background"
            style={{ padding: 24, minHeight: 360 }}
          >
            <div className="userHeader">
              <Search
                placeholder=""
                enterButton
                value={queryInfo.query}
                onChange={changeSearchValue}
                className="search-left"
                size="large"
              />
            </div>
            {/*  */}
            <Table
              dataSource={props.orders.goods}
              columns={colums}
              bordered
              pagination={false}
              rowKey="order_id"
            />
            {/*  */}
            <Pagination
              total={props.orders.total}
              showTotal={(total) => `Total ${total} `}
              showSizeChanger
              defaultPageSize={5}
              pageSizeOptions={pageNums}
              showQuickJumper
              onChange={changePageNum}
            />
            {/*  */}
            <Modal
              title=""
              visible={addvisible}
              onOk={addAddressColsed}
              onCancel={addAddressColsed}
            >
              <Form
                name="basic"
                labelCol={{
                  span: 5,
                }}
                wrapperCol={{
                  span: 19,
                }}
                initialValues={{
                  remember: true,
                }}
                form={form}
                autoComplete="off"
              >
                <Form.Item
                  label="/"
                  name="address1"
                  rules={[
                    {
                      required: true,
                      message: "/",
                    },
                  ]}
                  validateTrigger="onBlur"
                >
                  <Cascader
                    options={cityData}
                    expandTrigger="hover"
                    placeholder="Select"
                  />
                </Form.Item>
                <Form.Item
                  label=""
                  name="address2"
                  rules={[
                    {
                      required: true,
                      message: "",
                    },
                  ]}
                >
                  <Input placeholder="" />
                </Form.Item>
              </Form>
            </Modal>
            {/*  */}
            <OrderStep
              isModalVisible={isModalVisible}
              fn={controlVisible}
              info={props.loginfo}
            />
          </div>
        </Content>
      </Layout>
    </>
  );
}
// stateredux
// 
let mapStateToProps = (state) => {
  if (state.Order.goods) {
    state.Order.goods.forEach(
      (item) => (item.create_time = formatDate(item.create_time))
    );
  }
  return {
    orders: state.Order,
    loginfo: state.Log,
  };
};
let mapDispatchToProps = (dispatch) => ({
  getOrders: (queryInfo) => dispatch(actions.GETORDER(queryInfo)),
  getLog: () => dispatch(actions.GETLOG()),
});

export default connect(mapStateToProps, mapDispatchToProps)(Order);
