import React, { useState, useEffect } from "react";
import {
  Layout,
  Breadcrumb,
  Table,
  Button,
  Tag,
  Input,
  Pagination,
  Modal,
  Form,
  Cascader,
  message,
} from "antd";
import { EditOutlined, DeleteOutlined } from "@ant-design/icons";
import { getGoodsData } from "../../api/goods";
import { getCateList } from "../../api/cate";
import { formatDate } from "../../utils/utils";
import { upGoodsInfo } from "../../api/goods";
import _ from "lodash";
import { addGoods, delGoods } from "../../api/goods";
const { Content } = Layout;
const { Search } = Input;
export default function Goods(props) {
  const goHome = () => {
    props.history.push("/welcome");
  };
  const colums = [
    {
      title: "",
      dataIndex: "",
      key: "id",
      render: (text, record, index) => {
        return (
          <>
            <Tag color="purple" key={record.id}>
              {index + 1}
            </Tag>
          </>
        );
      },
    },
    {
      title: "",
      dataIndex: "goods_name",
      key: "goods_name",
    },
    {
      title: "()",
      dataIndex: "goods_price",
      key: "goods_price",
      width: 120,
    },
    {
      title: "(Kg)",
      dataIndex: "goods_weight",
      key: "goods_weight",
      width: 120,
    },
    {
      title: "",
      dataIndex: "upd_time",
      key: "upd_time",
      width: 150,
    },
    {
      title: "",
      key: "action",
      render: (record) => {
        return (
          <div className="operation">
            <Button
              icon={<EditOutlined />}
              size="mini"
              type="primary"
              onClick={() => showEditBox(record)}
            >
              
            </Button>
            <Button
              type="danger"
              icon={<DeleteOutlined />}
              size="mini"
              onClick={() => showDelbox(record.goods_id)}
            >
              
            </Button>
          </div>
        );
      },
    },
  ];

  // 
  const [queryInfo, setQueryInfo] = useState({
    query: "",
    pagenum: 1,
    pagesize: 10,
  });
  // 
  const [tableData, setTableData] = useState([]);
  // 
  const [pageNums] = useState([5, 10, 15, 20]);
  // 
  const [cateList, setcateList] = useState([]);
  // 
  const [addvisible, setaddvisible] = useState(false);
  // 
  const [delvisible, setdelvisible] = useState(false);
  // 
  const [editvisible, seteditvisible] = useState(false);
  // /id
  const [userId, setUserid] = useState("");
  // 
  const [adduserInfo, setadduserInfo] = useState({
    goods_name: "",
    goods_price: "",
    goods_weight: "",
    goods_number: "",
    // 
    goods_cat: "",
  });
  // 
  const [edituserinfo, setedituserinfo] = useState({});
  // 
  const [form] = Form.useForm();
  const [editForm] = Form.useForm();
  // 
  useEffect(() => {
    // 
    getGoodsData(queryInfo).then((res) => {
      res.data.goods.forEach(
        (item) => (item.upd_time = formatDate(item.upd_time))
      );
      setTableData(res.data);
    });
  }, [queryInfo, addvisible, delvisible, editvisible]);

  // 
  useEffect(() => {
    // 
    getCateList().then((res) => {
      setcateList(res.data);
    });
  }, []);

  // 
  function changePageNum(page, pageSize) {
    setQueryInfo({
      ...queryInfo,
      pagenum: page,
      pagesize: pageSize,
    });
  }

  // ()
  function changeSearchValue(e) {
    setQueryInfo({
      ...queryInfo,
      query: e.target.value,
    });
  }
  // 
  // const debounceSearch = _.debounce(changeSearchValue, 1000);
  //
  async function calcelAdd() {
    await setaddvisible(false);
    form.resetFields();
  }

  // 
  async function addConfirm() {
    try {
      const value = await form.validateFields();
      const res = await addGoods(adduserInfo);
      // console.log(res);
      if (res.meta.status === 201) {
        message.success("");
      } else {
        message.error("");
        // console.log(res);
      }
      setaddvisible(false);
      form.resetFields();
    } catch (err) {
      message.error("");
      console.log(err);
    }
  }

  // 
  function changeUserInfo(changedValues, allValues) {
    setadduserInfo(allValues);
  }

  // 
  function changeCate(value) {
    console.log(value);
    setadduserInfo({
      ...adduserInfo,
      goods_cat: value.join(","),
    });
  }
  // id
  async function showDelbox(id) {
    setdelvisible(true);
    setUserid(id);
  }
  // 
  async function delConfirm() {
    try {
      const res = await delGoods(userId);
      if (res.meta.status === 200) {
        message.success("");
      } else {
        message.error("");
      }
      setdelvisible(false);
    } catch (error) {
      message.error("");
    }
  }
  // 
  function calcelDel() {
    setdelvisible(false);
    message.warning("");
  }
  // form
  async function showEditBox(info) {
    seteditvisible(true);
    editForm.setFieldsValue({
      ...edituserinfo,
      goods_cat: "0,0,0",
      goods_name: info.goods_name,
      goods_price: info.goods_price,
      goods_weight: info.goods_weight,
      goods_number: info.goods_number,
    });
    // 
    setedituserinfo(info);
  }
  // 
  function editUserInfo(changedValues, allValues) {
    setedituserinfo({
      ...edituserinfo,
      goods_cat: "0,0,0",
      goods_name: allValues.goods_name,
      goods_price: allValues.goods_price,
      goods_weight: allValues.goods_weight,
      goods_number: allValues.goods_number,
    });
  }
  // 
  async function EditConfirm() {
    try {
      await editForm.validateFields();
      const res = await upGoodsInfo(edituserinfo.goods_id, edituserinfo);
      if (res.meta.status === 200) {
        message.success("");
      } else {
        message.error("");
      }
      seteditvisible(false);
    } catch (err) {
      message.error("");
      console.log(err);
    }
  }
  return (
    <Layout className="site-layout">
      <Content style={{ margin: " 16px" }}>
        {/*  */}
        <Breadcrumb style={{ margin: "16px 0" }}>
          <Breadcrumb.Item onClick={goHome}></Breadcrumb.Item>
          <Breadcrumb.Item></Breadcrumb.Item>
          <Breadcrumb.Item></Breadcrumb.Item>
        </Breadcrumb>
        {/*  */}
        <div
          className="site-layout-background"
          style={{ padding: 24, minHeight: 360 }}
        >
          <div className="userHeader">
            {/* */}
            <Search
              placeholder=""
              enterButton
              value={queryInfo.query}
              onChange={changeSearchValue}
              className="search-left"
              size="large"
            />
            <Button
              type="primary"
              size="large"
              onClick={() => setaddvisible(true)}
            >
              
            </Button>
          </div>
          {/*  */}
          <Table
            dataSource={tableData.goods}
            columns={colums}
            bordered
            pagination={false}
            rowKey="goods_id"
          />
          {/*  */}
          <Pagination
            total={tableData.total}
            showTotal={(total) => `Total ${total} `}
            showSizeChanger
            defaultPageSize={10}
            pageSizeOptions={pageNums}
            showQuickJumper
            onChange={changePageNum}
          />
          {/*  */}
          <Modal
            title=""
            visible={addvisible}
            onOk={addConfirm}
            onCancel={calcelAdd}
          >
            <Form
              name="basic"
              labelCol={{
                span: 5,
              }}
              wrapperCol={{
                span: 19,
              }}
              initialValues={{
                remember: true,
              }}
              form={form}
              onValuesChange={changeUserInfo}
              autoComplete="off"
            >
              <Form.Item
                label=""
                name="goods_name"
                rules={[
                  {
                    required: true,
                    message: "",
                  },
                ]}
                validateTrigger="onBlur"
                key="name"
              >
                <Input />
              </Form.Item>
              <Form.Item
                label="()"
                name="goods_price"
                key="price"
                rules={[
                  {
                    required: true,
                    message: "",
                  },
                  {
                    type: "string",
                    message: "",
                    pattern: new RegExp(/^\d{1,8}$/),
                  },
                ]}
                validateTrigger="onBlur"
              >
                <Input />
              </Form.Item>
              <Form.Item
                label="()"
                name="goods_number"
                key="number"
                rules={[
                  {
                    required: true,
                    message: "",
                  },
                  {
                    type: "string",
                    message: "",
                    pattern: new RegExp(/^\d{1,8}$/),
                  },
                ]}
                validateTrigger="onBlur"
              >
                <Input />
              </Form.Item>
              <Form.Item
                label="(KG)"
                name="goods_weight"
                key="weight"
                rules={[
                  {
                    required: true,
                    message: "",
                  },
                  {
                    type: "string",
                    message: "",
                    pattern: new RegExp(/^\d{1,8}$/),
                  },
                ]}
                validateTrigger="onBlur"
              >
                <Input />
              </Form.Item>
              <Form.Item
                label=""
                name="goods_cat"
                key="cat"
                rules={[
                  {
                    required: true,
                    message: "",
                  },
                ]}
              >
                <Cascader
                  options={cateList}
                  expandTrigger="hover"
                  fieldNames={{
                    label: "cat_name",
                    value: "cat_name",
                    children: "children",
                  }}
                  placeholder=""
                  onChange={changeCate}
                />
              </Form.Item>
            </Form>
          </Modal>
          {/*  */}
          <Modal
            title=""
            visible={delvisible}
            onOk={delConfirm}
            onCancel={calcelDel}
            okText=""
            cancelText=""
          >
            <p>, ?</p>
          </Modal>
          {/*  */}
          <Modal
            title=""
            visible={editvisible}
            onOk={EditConfirm}
            onCancel={() => seteditvisible(false)}
            okText=""
            cancelText=""
          >
            <Form
              name="edit"
              labelCol={{
                span: 5,
              }}
              wrapperCol={{
                span: 19,
              }}
              initialValues={{
                goods_name: "",
                goods_price: "",
                goods_weight: "",
                goods_number: "",
              }}
              form={editForm}
              onValuesChange={editUserInfo}
              autoComplete="off"
            >
              <Form.Item
                label=""
                name="goods_name"
                key="index"
                rules={[
                  {
                    required: true,
                    message: "",
                  },
                ]}
                validateTrigger="onBlur"
              >
                <Input />
              </Form.Item>
              <Form.Item
                label="()"
                name="goods_price"
                key="id1"
                rules={[
                  {
                    required: true,
                    message: "",
                  },
                  {
                    message: "",
                    pattern: new RegExp(/^\d{1,8}$/),
                  },
                ]}
                validateTrigger="onBlur"
              >
                <Input />
              </Form.Item>
              <Form.Item
                label="()"
                name="goods_number"
                key="id2"
                rules={[
                  {
                    required: true,
                    message: "",
                  },
                  {
                    message: "",
                    pattern: new RegExp(/^\d{1,8}$/),
                  },
                ]}
                validateTrigger="onBlur"
              >
                <Input />
              </Form.Item>
              <Form.Item
                label="(KG)"
                name="goods_weight"
                key="id3"
                rules={[
                  {
                    required: true,
                    message: "",
                  },
                  {
                    message: "",
                    pattern: new RegExp(/^\d{1,8}$/),
                  },
                ]}
                validateTrigger="onBlur"
              >
                <Input />
              </Form.Item>
            </Form>
          </Modal>
        </div>
      </Content>
    </Layout>
  );
}
