import React from "react";
import {
  Table,
  message,
  Switch,
  Button,
  Tooltip,
  Pagination,
  Modal,
  Form,
  Input,
  Select,
} from "antd";
import {
  EditOutlined,
  DeleteOutlined,
  EllipsisOutlined,
} from "@ant-design/icons";
import "./UserTables.css";
import {
  getUser,
  delUser,
  changeState,
  changeUser,
  changeRole,
} from "../../api/userChange";
import { getRoles } from "../../api/roles";
const { Column } = Table;
const { Option } = Select;
const pageNums = [2, 5, 10, 50];
class Tables extends React.Component {
  constructor(props) {
    super();
    this.state = {
      // 
      queryInfo: { query: "", pagenum: 1, pagesize: 2 },
      // 
      tableData: {
        users: [],
        total: 0,
      },
      // 
      showChangeUser: false,
      // 
      showDelUser: false,
      // 
      showChangeRole: false,
      // id
      delUserId: "",
      // 
      userInfo: {},
      // 
      changeRoleInfo: {},
      // 
      RoleList: [],
      // 
      roles: "",
    };
    this.formRef = React.createRef();
  }
  componentDidMount() {
    this.showTable();
  }

  // 
  showTable = async () => {
    const res = await getUser(this.state.queryInfo);
    if (res.meta.status !== 200) {
      return message.error("");
    } else {
      this.setState({
        tableData: res.data,
      });
    }
  };
  // 
  getData = (value) => {
    this.setState({
      tableData: value,
    });
  };
  // 
  changeStatu = async (record, text, checked) => {
    console.log(text);
    let res = await changeState(record.id, checked);
    if (res.meta.status !== 200) {
      record.mg_state = !record.mg_state;
      message.error("");
    } else {
      message.success("");
    }
  };
  // 
  changePageNum = async (pageNumber, pageSize) => {
    this.setState(
      {
        queryInfo: {
          ...this.state.queryInfo,
          pagenum: pageNumber,
          pagesize: pageSize,
        },
      },
      () => {
        this.showTable();
      }
    );
  };
  // 
  confirmDel = async () => {
    const res = await delUser(this.state.delUserId);
    if (res.meta.status !== 200) {
      message.error("");
    } else {
      message.success("");
      this.showDelUser(false, "");
      this.showTable();
    }
  };
  // 
  showDelUser = (showDelUser, delUserId) => {
    if (delUserId === undefined) {
      message.warning("");
    }
    this.setState({ showDelUser, delUserId });
  };
  // 
  changeUserDialog = (showChangeUser, userInfo) => {
    this.setState({ showChangeUser, userInfo });
  };
  // 
  changeVal = (ev) => {
    this.setState({
      userInfo: {
        ...this.state.userInfo,
        [ev.target.name]: ev.target.value,
      },
    });
  };
  // 
  onFinish = async () => {
    try {
      await this.formRef.current.validateFields();
      const res = await changeUser(this.state.userInfo.id, {
        email: this.state.userInfo.email,
        mobile: this.state.userInfo.mobile,
      });
      if (res.meta.status === 200) {
        message.success("");
        this.changeUserDialog(false, {});
        this.showTable();
      } else {
        message.error("");
      }
    } catch {
      message.error("");
    }
  };
  // 
  getRolesList = async () => {
    try {
      const res = await getRoles();
      this.setState({ RoleList: res.data });
    } catch (err) {
      message.error("");
    }
  };
  // 
  showChangeRoleDialog = async (showChangeRole, changeRoleInfo) => {
    this.setState({ showChangeRole, changeRoleInfo }, () => {
      this.getRolesList();
    });
  };
  // 
  handleChange = (roles) => {
    this.setState({ roles });
  };
  // 
  confirmChangeRole = async () => {
    try {
      const res = await changeRole(this.state.changeRoleInfo.id, {
        rid: this.state.RoleList.filter(
          (item) => item.roleName === this.state.roles
        )[0].id,
      });
      if (res.meta.status === 200) {
        message.success("");
        this.showChangeRoleDialog(false, {});
      } else {
        message.error("");
      }
    } catch (error) {
      message.error("");
    }
  };
  render() {
    return (
      <>
        {/*  */}
        <Table
          bordered
          dataSource={this.state.tableData.users}
          pagination={false}
          rowKey="id"
        >
          <Column
            title="#"
            key="id"
            render={(text, record, index) => (
              <>
                <span>{index + 1}</span>
              </>
            )}
          />
          <Column title="" dataIndex="username" key="id" />
          <Column title="" dataIndex="email" key="id" />
          <Column title="" dataIndex="mobile" key="id" />
          <Column title="" dataIndex="role_name" key="id" />
          <Column
            title=""
            dataIndex="mg_state"
            key="id"
            render={(text, record) => (
              <>
                <Switch
                  onChange={(checked) =>
                    this.changeStatu(record, text, checked)
                  }
                  defaultChecked={record.mg_state}
                />
              </>
            )}
          />
          <Column
            className="operation"
            title=""
            key="id"
            render={(text, record) => (
              <>
                {/*  */}
                <Button
                  icon={<EditOutlined />}
                  size="mini"
                  className="warning"
                  onClick={() => this.changeUserDialog(true, text)}
                ></Button>
                {/* / */}

                <Button
                  type="danger"
                  icon={<DeleteOutlined />}
                  size="mini"
                  onClick={() => this.showDelUser(true, text.id)}
                ></Button>

                <Tooltip title="">
                  <Button
                    type="primary"
                    icon={<EllipsisOutlined />}
                    size="mini"
                    onClick={() => this.showChangeRoleDialog(true, text)}
                  ></Button>
                </Tooltip>
              </>
            )}
          />
        </Table>
        {/*  */}
        <Pagination
          total={this.state.tableData.total}
          showTotal={(total) => `Total ${total} `}
          showSizeChanger
          defaultPageSize={2}
          pageSizeOptions={pageNums}
          showQuickJumper
          onChange={this.changePageNum}
        />
        {/*  */}
        <Modal
          title=""
          centered
          visible={this.state.showChangeUser}
          onOk={this.onFinish}
          onCancel={() => this.changeUserDialog(false, {})}
          okText=""
          cancelText=""
        >
          <Form
            name="basic"
            ref={this.formRef}
            labelCol={{
              span: 4,
            }}
            wrapperCol={{
              span: 20,
            }}
            onFinish={this.onFinish}
            autoComplete="off"
            initialValues={this.state.userInfo}
          >
            <Form.Item
              label=""
              rules={[
                {
                  required: true,
                  message: "",
                },
              ]}
            >
              <Input value={this.state.userInfo.username} disabled />
            </Form.Item>
            <Form.Item
              label=""
              rules={[
                {
                  required: true,
                  message: "",
                },
                {
                  type: "string",
                  message: "",
                  pattern: new RegExp(
                    /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(\.[a-zA-Z0-9_-])+/
                  ),
                },
              ]}
              validateTrigger="onBlur"
            >
              <Input
                value={this.state.userInfo.email}
                name="email"
                onChange={this.changeVal}
              />
            </Form.Item>

            <Form.Item
              label=""
              rules={[
                {
                  required: true,
                  message: "",
                },
                {
                  type: "string",
                  message: "",
                  min: 1,
                  max: 11,
                  pattern: new RegExp(/^1\d{10}$/),
                },
              ]}
              validateTrigger="onBlur"
            >
              <Input
                value={this.state.userInfo.mobile}
                name="mobile"
                onChange={this.changeVal}
              />
            </Form.Item>
          </Form>
        </Modal>
        {/*  */}
        <Modal
          title=""
          centered
          visible={this.state.showDelUser}
          onOk={this.confirmDel}
          onCancel={() => this.showDelUser(false)}
          okText=""
          cancelText=""
        >
          <p>, ?</p>
        </Modal>
        {/*  */}
        <Modal
          title=""
          centered
          visible={this.state.showChangeRole}
          onOk={this.confirmChangeRole}
          onCancel={() => this.showChangeRoleDialog(false, {})}
          okText=""
          cancelText=""
        >
          <p>{this.state.changeRoleInfo.username}</p>
          <p> {this.state.changeRoleInfo.role_name}</p>
          <div>
            
            <Select
              defaultValue=""
              style={{ width: 120 }}
              onChange={this.handleChange}
            >
              {this.state.RoleList.map((roles) => (
                <Option value={roles.roleName} key={roles.id}>
                  {roles.roleName}
                </Option>
              ))}
            </Select>
          </div>
        </Modal>
      </>
    );
  }
}

export default Tables;
