import React, { useState, useEffect } from "react";
import {
  Layout,
  Breadcrumb,
  Table,
  Button,
  Tag,
  Pagination,
  Modal,
  Form,
  Cascader,
  message,
  Input,
} from "antd";
import {
  EditOutlined,
  DeleteOutlined,
  CheckCircleTwoTone,
  CloseSquareTwoTone,
} from "@ant-design/icons";
import store from "../../store";
import * as actions from "../../store/actions/actions";
import { getCateList, addCate, delCate, editCata } from "../../api/cate";
const { Content } = Layout;
export default function Cates(props) {
  const goHome = () => {
    props.history.push("/welcome");
  };
  const colums = [
    {
      title: "",
      dataIndex: "",
    },
    {
      title: "#",
      dataIndex: "",
      render: (text, record, index) => {
        return (
          <>
            <span>{index + 1}</span>
          </>
        );
      },
    },
    {
      title: "",
      dataIndex: "cat_name",
    },
    {
      title: "",
      dataIndex: "cat_deleted",
      width: 280,
      render: (text) => (
        <>
          <Tag visible={text === false}>
            <CheckCircleTwoTone />
          </Tag>
          <Tag visible={text === true}>
            <CloseSquareTwoTone />
          </Tag>
        </>
      ),
    },
    {
      title: "",
      dataIndex: "cat_level",
      width: 280,
      render: (text) => (
        <>
          <Tag color="orange" visible={Number(text) === 0}>
            
          </Tag>
          <Tag color="purple" visible={Number(text) === 1}>
            
          </Tag>
          <Tag color="magenta" visible={Number(text) === 2}>
            
          </Tag>
        </>
      ),
    },

    {
      title: "",
      dataIndex: "action",
      width: 280,
      render: (text, record) => (
        <div className="operation">
          <Button
            icon={<EditOutlined />}
            size="mini"
            type="primary"
            onClick={() => showEditBox(record)}
          >
            
          </Button>
          <Button
            type="danger"
            icon={<DeleteOutlined />}
            size="mini"
            onClick={() => showDelbox(record.cat_id)}
          >
            
          </Button>
        </div>
      ),
    },
  ];
  // 
  const [queryInfo, setQueryInfo] = useState({
    query: "",
    pagenum: 1,
    pagesize: 5,
  });
  // 
  const [tableData, setTableData] = useState([]);
  // 
  const [cateList, setcateList] = useState([]);
  // 
  const [pageNums] = useState([3, 5, 10, 15]);
  // 
  const [addvisible, setaddvisible] = useState(false);
  // 
  const [form] = Form.useForm();
  const [editForm] = Form.useForm();
  // 
  const [adduserInfo, setadduserInfo] = useState({
    cat_name: "",
    // 
    selectKeys: [],
  });
  // 
  const [delvisible, setdelvisible] = useState(false);
  // /id
  const [userId, setUserid] = useState("");
  // 
  const [editvisible, seteditvisible] = useState(false);
  // 
  const [edituserinfo, setedituserinfo] = useState({});

  // 
  useEffect(() => {
    // reduxaction
    store.dispatch(actions.GETCATES());
    // 
    store.subscribe(() => {
      setTableData(store.getState().Cate);
    });
  }, []);
  //
  useEffect(() => {
    getCateList({ type: 2 }).then((res) => setcateList(res.data));
  }, []);
  // 
  useEffect(() => {
    getCateList(queryInfo).then((res) => setTableData(res.data));
  }, [queryInfo, addvisible, delvisible, editvisible]);

  // 
  function changePageNum(page, pageSize) {
    setQueryInfo({
      ...queryInfo,
      pagenum: page,
      pagesize: pageSize,
    });
  }
  // 
  function changeUserInfo(changedValues, allValues) {
    setadduserInfo(allValues);
  }
  //
  async function calcelAdd() {
    await setaddvisible(false);
    form.resetFields();
  }
  // 
  async function addConfirm() {
    await form.validateFields();
    try {
      const res = await addCate({
        cat_pid: adduserInfo.selectKeys[adduserInfo.selectKeys.length - 1],
        cat_name: adduserInfo.cat_name,
        cat_level: adduserInfo.selectKeys.length,
      });
      if (res.meta.status === 201) {
        message.success("");
      } else {
        message.error("");
      }
      setaddvisible(false);
      form.resetFields();
    } catch (err) {
      message.error("");
    }
  }
  // id
  async function showDelbox(id) {
    setdelvisible(true);
    setUserid(id);
  }
  // 
  function calcelDel() {
    setdelvisible(false);
    message.warning("");
  }
  // 
  async function delConfirm() {
    try {
      const res = await delCate(userId);
      if (res.meta.status === 200) {
        message.success("");
      } else {
        message.error("");
      }
      setdelvisible(false);
    } catch (error) {
      message.error("");
    }
  }
  // form
  async function showEditBox(info) {
    seteditvisible(true);
    editForm.setFieldsValue({
      cat_name: info.cat_name,
    });
    // 
    setedituserinfo(info);
  }
  // 
  function editUserInfo(changedValues, allValues) {
    setedituserinfo({
      ...edituserinfo,
      cat_name: allValues.cat_name,
    });
  }
  // 
  async function EditConfirm() {
    try {
      await editForm.validateFields();
      const res = await editCata(edituserinfo.cat_id, edituserinfo);
      console.log(res);
      if (res.meta.status === 200) {
        message.success("");
      } else {
        message.error("");
      }
      seteditvisible(false);
    } catch (err) {
      message.error("");
    }
  }
  return (
    <Layout className="site-layout">
      <Content style={{ margin: " 16px" }}>
        {/*  */}
        <Breadcrumb style={{ margin: "16px 0" }}>
          <Breadcrumb.Item onClick={goHome}></Breadcrumb.Item>
          <Breadcrumb.Item></Breadcrumb.Item>
          <Breadcrumb.Item></Breadcrumb.Item>
        </Breadcrumb>
        {/*  */}
        <div
          className="site-layout-background"
          style={{ padding: 24, minHeight: 360 }}
        >
          <div className="userHeader">
            {/* */}
            <Button
              type="primary"
              size="large"
              onClick={() => setaddvisible(true)}
            >
              
            </Button>
          </div>
          {/*  */}
          <Table
            dataSource={tableData.result}
            columns={colums}
            bordered
            pagination={false}
            key="id"
            rowKey="cat_id"
          />
          {/*  */}
          <Pagination
            total={tableData.total}
            showTotal={(total) => `Total ${total} `}
            showSizeChanger
            defaultPageSize={5}
            pageSizeOptions={pageNums}
            showQuickJumper
            onChange={changePageNum}
          />
          {/*  */}
          <Modal
            title=""
            visible={addvisible}
            onOk={addConfirm}
            onCancel={calcelAdd}
          >
            <Form
              name="basic"
              labelCol={{
                span: 5,
              }}
              wrapperCol={{
                span: 19,
              }}
              initialValues={{
                remember: true,
              }}
              form={form}
              onValuesChange={changeUserInfo}
              autoComplete="off"
            >
              <Form.Item
                label=""
                name="cat_name"
                rules={[
                  {
                    required: true,
                    message: "",
                  },
                ]}
                validateTrigger="onBlur"
              >
                <Input />
              </Form.Item>
              <Form.Item
                label=""
                name="selectKeys"
                rules={[
                  {
                    required: true,
                    message: "",
                  },
                ]}
              >
                <Cascader
                  options={cateList}
                  expandTrigger="hover"
                  fieldNames={{
                    label: "cat_name",
                    value: "cat_id",
                    children: "children",
                  }}
                  placeholder=""
                />
              </Form.Item>
            </Form>
          </Modal>
          {/*  */}
          <Modal
            title=""
            visible={delvisible}
            onOk={delConfirm}
            onCancel={calcelDel}
            okText=""
            cancelText=""
          >
            <p>, ?</p>
          </Modal>
          {/*  */}
          <Modal
            title=""
            visible={editvisible}
            onOk={EditConfirm}
            onCancel={() => seteditvisible(false)}
            okText=""
            cancelText=""
          >
            <Form
              name="edit"
              labelCol={{
                span: 5,
              }}
              wrapperCol={{
                span: 19,
              }}
              initialValues={{
                cat_name: "",
              }}
              form={editForm}
              onValuesChange={editUserInfo}
              autoComplete="off"
            >
              <Form.Item
                label=""
                name="cat_name"
                rules={[
                  {
                    required: true,
                    message: "",
                  },
                ]}
                validateTrigger="onBlur"
              >
                <Input />
              </Form.Item>
            </Form>
          </Modal>
        </div>
      </Content>
    </Layout>
  );
}
