// path
import UserS from "../pages/User/User.jsx";
import Roles from "../pages/Roles/Roles";
import Rights from "../pages/Rights/Rights";
import Goods from "../pages/Goods/Goods";
import Categories from "../pages/Categories/Categories";
import Orders from "../pages/Orders/Orders";
import Reports from "../pages/Reports/Reports";

// 1

let routes = [
  {
    authName: "",
    path: "/users",
    id: "01",
    children: [
      {
        id: "01001",
        authName: "",
        children: [],
        path: "/users",
        component: UserS,
      },
    ],
  },
  {
    id: "02",
    authName: "",
    path: "/rights",
    children: [
      {
        id: "02001",
        authName: "",
        children: [],
        path: "/roles",
        component: Roles,
      },
      {
        id: "02002",
        authName: "",
        children: [],
        path: "/rights",
        component: Rights,
      },
    ],
  },
  {
    id: "03",
    authName: "",
    path: "/goods",
    children: [
      {
        id: "03001",
        authName: "",
        children: [],
        path: "/goods",
        component: Goods,
      },
      {
        id: "03002",
        authName: "",
        children: [],
        path: "/categories",
        component: Categories,
      },
    ],
  },
  {
    id: "04",
    authName: "",
    path: "/orders",
    children: [
      {
        id: "04001",
        authName: "",
        children: [],
        path: "/orders",
        component: Orders,
      },
    ],
  },
  {
    id: "05",
    authName: "",
    path: "/reports",
    children: [
      {
        id: "05001",
        authName: "",
        children: [],
        path: "/reports",
        component: Reports,
      },
    ],
  },
];

// 2path

const grants = (houPaths) => {
  return routes.filter((route) => houPaths.some((item) => item === route.path));
};

export default grants;
