import React, { Component } from "react";
import {
  Layout,
  Breadcrumb,
  message,
  Input,
  Button,
  Table,
  Modal,
  Form,
  Tree,
  Tag,
} from "antd";
import {
  EditOutlined,
  DeleteOutlined,
  SettingOutlined,
} from "@ant-design/icons";
import {
  getRoles,
  changeRoles,
  delRoles,
  addRoles,
  rightsTree,
  delTag,
} from "../../api/roles";
import "./Roles.css";
const { Content } = Layout;
const { Column } = Table;
class Roles extends Component {
  constructor(props) {
    super();
    this.state = {
      // 
      tableData: [],
      // 
      showChangeUser: false,
      // 
      showDelUser: false,
      // 
      showAddUser: false,
      // id
      delUserId: "",
      // 
      userInfo: {
        roleName: "",
        roleDesc: "",
      },
      // 
      addRoleInfo: {
        roleName: "",
        roleDesc: "",
      },
    };
    this.formRef = React.createRef();
    this.addformRef = React.createRef();
  }
  // 
  goHome = () => {
    this.props.history.push("/welcome");
  };
  componentDidMount = () => {
    this.getRolesData();
  };
  // 
  getRolesData = async () => {
    const res = await getRoles();
    // console.log(res.data);
    this.setState({ tableData: res.data });
  };
  // 
  changeUserDialog = (showChangeUser, userInfo) => {
    this.setState({ showChangeUser, userInfo });
  };
  // 
  changeVal = (ev) => {
    this.setState({
      userInfo: {
        ...this.state.userInfo,
        [ev.target.name]: ev.target.value,
      },
    });
  };
  // 
  onFinish = async () => {
    try {
      await this.formRef.current.validateFields();
      console.log(this.state.userInfo);
      const res = await changeRoles(this.state.userInfo.id, {
        roleName: this.state.userInfo.roleName,
        roleDesc: this.state.userInfo.roleDesc,
      });
      if (res.meta.status === 200) {
        message.success("");
        this.changeUserDialog(false, {});
        this.getRolesData();
      } else {
        message.error("");
      }
    } catch {
      message.error("");
    }
  };
  // 
  confirmDel = async () => {
    const res = await delRoles(this.state.delUserId);
    if (res.meta.status !== 200) {
      message.error("");
    } else {
      message.success("");
      this.showDelUser(false, "");
      this.getRolesData();
    }
  };

  // 
  showDelUser = (showDelUser, delUserId) => {
    if (delUserId === undefined) {
      message.warning("");
    }
    this.setState({ showDelUser, delUserId });
  };
  // ;
  addRoleDialog = (showAddUser) => {
    this.setState({ showAddUser }, () => {
      this.addformRef.current.resetFields();
    });
  };
  // 
  AddChangeVal = (changedValues, allValues) => {
    this.setState({
      addRoleInfo: allValues,
    });
  };
  // ;
  onFinishAddRoles = async () => {
    try {
      const value = await this.addformRef.current.validateFields();
      const res = await addRoles(this.state.addRoleInfo);
      if (res.meta.status === 201) {
        message.success("");
        this.addRoleDialog(false);
        this.getRolesData();
      } else {
        message.error("");
      }
    } catch {
      message.error("");
    }
  };
  // 
  async delTag(info, id) {
    const res = await delTag(info.id, id);
    // console.log(res);
    if (res.meta.status === 200) {
      message.success("");
      this.getRolesData();
    } else {
      message.error("");
    }
  }

  componentWillUnmount = () => {
    this.setState = (state, callback) => {
      return;
    };
  };
  render() {
    return (
      <>
        <Layout className="site-layout">
          <Content style={{ margin: " 16px" }}>
            {/*  */}
            <Breadcrumb style={{ margin: "16px 0" }}>
              <Breadcrumb.Item onClick={this.goHome}></Breadcrumb.Item>
              <Breadcrumb.Item></Breadcrumb.Item>
              <Breadcrumb.Item></Breadcrumb.Item>
            </Breadcrumb>
            {/*  */}
            <div
              className="site-layout-background"
              style={{ padding: 24, minHeight: 360 }}
            >
              <div className="userHeader">
                <Button
                  type="primary"
                  size="large"
                  onClick={() => this.addRoleDialog(true)}
                >
                  
                </Button>
              </div>
              {/*  */}
              <Table
                bordered
                dataSource={this.state.tableData}
                pagination={false}
                rowKey="id"
                expandable={{
                  childrenColumnName: "",
                  expandedRowRender: (record) => (
                    <>
                      {/*  */}
                      {record.children.map((first) => (
                        <div key={first.id} className="expanded">
                          <div key={first.id} className="firstexpanded">
                            <Tag
                              color="cyan"
                              key={first.id}
                              closable
                              onClose={() => this.delTag(record, first.id)}
                            >
                              {first.authName}
                            </Tag>
                          </div>
                          {/*  */}
                          <div className="secondexpanded">
                            {first.children.map((second) => (
                              <div key={second.id} className="thirdexpanded">
                                <div className="thirdexpandedLeft">
                                  <Tag
                                    color="blue"
                                    key={second.id}
                                    closable
                                    onClose={() =>
                                      this.delTag(record, second.id)
                                    }
                                  >
                                    {second.authName}
                                  </Tag>
                                </div>
                                {/*  */}
                                <div className="thirdexpandedRight">
                                  {second.children.map((third) => (
                                    <Tag
                                      color="volcano"
                                      key={third.id}
                                      closable
                                      onClose={() =>
                                        this.delTag(record, third.id)
                                      }
                                    >
                                      {third.authName}
                                    </Tag>
                                  ))}
                                </div>
                              </div>
                            ))}
                          </div>
                        </div>
                      ))}
                    </>
                  ),
                }}
              >
                <Column
                  title=""
                  dataIndex="roleName"
                  key="id"
                  width={330}
                />
                <Column
                  title=""
                  dataIndex="roleDesc"
                  key="id"
                  width={330}
                />

                <Column
                  className="operation"
                  title=""
                  key="id"
                  width={250}
                  render={(text, record) => (
                    <>
                      {/*  */}
                      <Button
                        icon={<EditOutlined />}
                        size="mini"
                        type="primary"
                        onClick={() => this.changeUserDialog(true, text)}
                      >
                        
                      </Button>
                      {/* / */}

                      <Button
                        type="danger"
                        icon={<DeleteOutlined />}
                        size="mini"
                        onClick={() => this.showDelUser(true, text.id)}
                      >
                        
                      </Button>
                    </>
                  )}
                />
              </Table>
            </div>
            {/*  */}
            <Modal
              title=""
              centered
              visible={this.state.showChangeUser}
              onOk={this.onFinish}
              onCancel={() => this.changeUserDialog(false, {})}
              okText=""
              cancelText=""
            >
              <Form
                name="editRoles"
                ref={this.formRef}
                labelCol={{
                  span: 4,
                }}
                wrapperCol={{
                  span: 20,
                }}
                onFinish={this.onFinish}
                autoComplete="off"
                initialValues={this.state.userInfo}
              >
                <Form.Item label="">
                  <Input
                    value={this.state.userInfo.roleName}
                    name="roleName"
                    disabled
                    onChange={this.changeVal}
                  />
                </Form.Item>
                <Form.Item
                  label=""
                  rules={[
                    {
                      required: true,
                      message: "",
                    },
                    {
                      type: "string",
                      message: "3-20",
                      pattern: new RegExp(/^\w{3,20}$/),
                    },
                  ]}
                  validateTrigger="onBlur"
                >
                  <Input
                    value={this.state.userInfo.roleDesc}
                    name="roleDesc"
                    onChange={this.changeVal}
                  />
                </Form.Item>
              </Form>
            </Modal>
            {/*  */}
            <Modal
              title=""
              centered
              visible={this.state.showDelUser}
              onOk={this.confirmDel}
              onCancel={() => this.showDelUser(false)}
              okText=""
              cancelText=""
            >
              <p>, ?</p>
            </Modal>
            {/*  */}
            <Modal
              title=""
              centered
              visible={this.state.showAddUser}
              onOk={this.onFinishAddRoles}
              onCancel={() => this.addRoleDialog(false)}
              okText=""
              cancelText=""
            >
              <Form
                name="basic"
                ref={this.addformRef}
                labelCol={{
                  span: 4,
                }}
                wrapperCol={{
                  span: 20,
                }}
                initialValues={{
                  remember: true,
                }}
                autoComplete="off"
                onValuesChange={this.AddChangeVal}
              >
                <Form.Item
                  label=""
                  name="roleName"
                  rules={[
                    {
                      required: true,
                      message: "",
                    },
                    {
                      type: "string",
                      min: 3,
                      max: 8,
                      message: "3 8",
                      pattern: /\w$/,
                    },
                  ]}
                  validateTrigger="onBlur"
                >
                  <Input />
                </Form.Item>
                <Form.Item
                  label=""
                  name="roleDesc"
                  rules={[
                    {
                      required: true,
                      message: "",
                    },
                    {
                      type: "string",
                      min: 3,
                      max: 8,
                      message: "3 8",
                      pattern: /\w$/,
                    },
                  ]}
                  validateTrigger="onBlur"
                >
                  <Input />
                </Form.Item>
              </Form>
            </Modal>
          </Content>
        </Layout>
      </>
    );
  }
}

export default Roles;
