import React from "react";
import { Modal, Form, Input, message } from "antd";
import { addUser } from "../../api/userChange";
class userModel extends React.Component {
  formRef = React.createRef();
  constructor() {
    super();
    this.state = {
      showAddUser: false,
      userInfo: {
        username: "",
        password: "",
        email: "",
        mobile: "",
      },
    };
  }
  // 
  onFinish = async () => {
    try {
      await this.formRef.current.validateFields();
      const res = await addUser(this.state.userInfo);
      if (res.meta.status === 201) {
        message.success("");
        this.addUserDialog(false);
      } else {
        message.error("");
      }
    } catch {
      message.error("");
    }
  };
  // 
  addUserDialog = (showAddUser) => {
    this.setState({ showAddUser }, () => {
      this.formRef.current.resetFields();
    });
  };
  // 
  changeVal = (changedValues, allValues) => {
    this.setState({
      userInfo: allValues,
    });
  };

  render() {
    return (
      <>
        <Modal
          title=""
          centered
          visible={this.state.showAddUser}
          onOk={this.onFinish}
          onCancel={() => this.addUserDialog(false)}
          okText=""
          cancelText=""
          afterClose={() => this.props.refreshTable()}
        >
          <Form
            name="basic"
            ref={this.formRef}
            labelCol={{
              span: 4,
            }}
            wrapperCol={{
              span: 20,
            }}
            initialValues={{
              remember: true,
            }}
            autoComplete="off"
            onValuesChange={this.changeVal}
          >
            <Form.Item
              label=""
              name="username"
              rules={[
                {
                  required: true,
                  message: "",
                },
              ]}
              validateTrigger="onBlur"
            >
              <Input />
            </Form.Item>

            <Form.Item
              label=""
              name="password"
              rules={[
                {
                  required: true,
                  message: "",
                },
                {
                  type: "string",
                  min: 6,
                  max: 15,
                  message: "6,15,",
                  pattern: /\d{6}$/,
                },
              ]}
              validateTrigger="onBlur"
            >
              <Input.Password />
            </Form.Item>

            <Form.Item
              label=""
              name="email"
              rules={[
                {
                  required: true,
                  message: "",
                },
                {
                  type: "string",
                  message: "",
                  pattern: new RegExp(
                    /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(\.[a-zA-Z0-9_-])+/
                  ),
                },
              ]}
              validateTrigger="onBlur"
            >
              <Input />
            </Form.Item>

            <Form.Item
              label=""
              name="mobile"
              rules={[
                {
                  required: true,
                  message: "",
                },
                {
                  type: "string",
                  message: "",
                  min: 1,
                  max: 11,
                  pattern: new RegExp(/^1\d{10}$/),
                },
              ]}
              validateTrigger="onBlur"
            >
              <Input />
            </Form.Item>
          </Form>
        </Modal>
      </>
    );
  }
}

export default userModel;
